<template>
  <div style="max-width: 100%;box-sizing: border-box" class="deviceApplyDetail">
    <myHeader
      :isBack="true"
      backPath="/deviceApplyRecord"
      title="申请详情">
    </myHeader>
    <div class="seat-head"></div>
    
    <div class="list-block">
      <mt-cell title="养户：">{{deviceDetailsData.custName}}</mt-cell>
      <mt-cell title="申请时间：">{{deviceDetailsData.bizDate}}</mt-cell>
      <mt-cell title="累计已领设备金额：">{{deviceDetailsData.equipAmt+'元'}}</mt-cell>
    </div>
    
    <div class="service-apply-detail">
      <div class="top">
        <span>申请领用明细</span>
      </div>
      <div class="details-container" v-for="(item,index) in deviceDetailsData.entrys" :key="index">
        <div class="detail-item">
          <div class="left">
            <span>设备名称：</span>
            <span>{{item.materialName}}</span>
          </div>
          <div class="right">
            <span>数量：</span>
            <span>{{item.qty}}</span>
          </div>
        </div>
        <div class="detail-item">
          <div class="left">
            <span>计量单位：</span>
            <span>{{item.unit}}</span>
          </div>
          <div class="right">
            <span>规格：</span>
            <span>{{item.model}}</span>
          </div>
        </div>
        <div class="detail-item">
          <div class="left">
            <span>设备单价：</span>
            <span>{{item.price}}</span>
          </div>
          <div class="right">
            <span>金额：</span>
            <span>{{item.amount}}</span>
          </div>
        </div>
      </div>
    </div>
    
    <div class="select-type">
      <div class="title">
        是否委托他人领取
      </div>
      
      <div class="radio-select-box">
        <div class="radio-item">
          <span>否</span>
          <img v-if="!deviceDetailsData.isEntrust" src="../../../assets/select-icon.png">
          <img v-else src="../../../assets/noSelect-icon.png">
        </div>
        <div class="radio-item">
          <span>是</span>
          <img v-if="deviceDetailsData.isEntrust" src="../../../assets/select-icon.png">
          <img v-else src="../../../assets/noSelect-icon.png">
        </div>
      </div>
      
      <div v-if="deviceDetailsData.isEntrust">
        <mt-cell title="委托领取人姓名：">{{deviceDetailsData.entrustorName}}</mt-cell>
        <mt-cell title="委托领取人身份证号：">{{deviceDetailsData.entrustorIdCard}}</mt-cell>
      </div>
    
    </div>
    
    <div class="service-apply-detail" v-if="billStatusIndex==30">
      <div class="top">
        <span>实际领用明细</span>
      </div>
      <div class="details-container" v-for="(item,index) in deviceDetailsData.actEntrys" :key="index">
        <div class="detail-item">
          <div class="left">
            <span>设备名称：</span>
            <span>{{item.materialName}}</span>
          </div>
          <div class="right">
            <span>数量：</span>
            <span>{{item.qty}}</span>
          </div>
        </div>
<!--        <div class="detail-item">-->
<!--          <div class="left">-->
<!--            <span>计量单位：</span>-->
<!--            <span>{{item.unit}}</span>-->
<!--          </div>-->
<!--          <div class="right">-->
<!--            <span>规格：</span>-->
<!--            <span>{{item.model}}</span>-->
<!--          </div>-->
<!--        </div>-->
        <div class="detail-item">
          <div class="left">
            <span>设备单价：</span>
            <span>{{item.price}}</span>
          </div>
          <div class="right">
            <span>金额：</span>
            <span>{{item.amount}}</span>
          </div>
        </div>
      </div>
    </div>
    
    <Signature v-show="isShowSignature" ref="signature"></Signature>
    
    <SubmitBtn v-if="recordState != 2 && billStatusIndex==30"
               @click.native="submitData()">
    </SubmitBtn>
  
  </div>
</template>

<script>
  import myHeader from '@/components/Header'
  import Signature from '@/components/Signature'
  import SubmitBtn from '@/components/SubmitBtn'
  import {queryEquipmentApplyInfoById, saveSignerEquipApply} from '@/api/materialApply'
  import {getParam} from '@/utils/wxUtil'
  import {Indicator} from 'mint-ui'
  
  export default {
    name: 'deviceApplyDetail',
    data() {
      return {
        deviceDetailsData: {
          custName: '',
          bizDate: '',
          equipAmt: '',
          entrustorName: '',
          entrustorIdCard: '',
          isEntrust: false,
          entrys: [
            {
              // materialName: '',
              // qty: '',
              // unit: '',
              // model: '',
              // price: '',
              // amount: ''
            }
          ],
          actEntrys: []
        },
        rcordId: '',
        recordState: '',
        billStatusIndex: '',
        signerImgs: '',
        isShowSignature: true
      }
    },
    components: {
      myHeader,
      Signature,
      SubmitBtn
    },
    created() {
      this._queryEquipmentApplyInfoById();
      // this.rcordId = this.$route.params.rcordId;
      this.rcordId = getParam('rcordId').replace(/%3D/g, '=').trim();
      
      Indicator.open({
        text: '加载中...',
        spinnerType: 'fading-circle'
      });
    },
    watch: {
      recordState(newName, oldName) {
        // 已完成签名，只需要展示签名图片，不能编辑
        if (this.recordState == 2 && this.billStatusIndex == 30) {
          this.$nextTick(() => {
            this.isShowSignature = true;
            this.$refs.signature.fromDataURL(this.signerImgs);
            this.$refs.signature.disabled = true;
          });
        }
    
        if (this.billStatusIndex == 30 && this.recordState != 2) { // 已经审核，可以签字，可以编辑
          this.$nextTick(() => {
            this.isShowSignature = true;
            this.$refs.signature.disabled = false;
          });
        }
    
        if (this.billStatusIndex != 30) {  // 没有审核或者已经取消
          this.isShowSignature = false;
        }
    
      }
    },
    mounted() {
    
    },
    methods: {
      _queryEquipmentApplyInfoById() {
        // const rcordId = this.$route.params.rcordId;
        const rcordId = getParam('rcordId').replace(/%3D/g, '=').trim();
        
        queryEquipmentApplyInfoById(rcordId).then(res => {
          Indicator.close();
          if (res.data) {
            let data = res.data;
            data.billStatusIndex ? this.billStatusIndex = data.billStatusIndex : null;
            data.state ? this.recordState = data.state : null;
            data.signerUrl.length ? this.signerImgs = data.signerUrl[0].img : null;
  
            let newEntrysList = [];
            let newActEntrysList = [];
            if (data.entrys && data.entrys.length) {
              newEntrysList = data.entrys.map(item => {
                return {
                  materialName: item.materialName,
                  qty: item.qty,
                  unit: item.unit,
                  model: item.model,
                  price: item.price,
                  amount: item.amount
                }
              });
            }
  
            if (data.actEntrys && data.actEntrys.length) {
              newActEntrysList = data.actEntrys.map(item => {
                return {
                  materialName: item.materialName,
                  qty: item.qty,
                  unit: item.unit,
                  model: item.model,
                  price: item.price,
                  amount: item.amount
                }
              });
            }
  
            this.deviceDetailsData = {
              custName: data.custName,
              bizDate: data.bizDate,
              equipAmt: data.equipAmt,
              entrustorName: data.entrustorName,
              entrustorIdCard: data.entrustorIdCard,
              isEntrust: data.isEntrust,
              entrys: newEntrysList,
              actEntrys: newActEntrysList,
            };
            
          }
        }).catch(err => {
          Indicator.close();
        });
      },
      submitData() {
        let signerImg = this.$refs.signature.save();
        
        const data = {
          rcordId: this.rcordId,
          fileEntry: {
            imgContent: signerImg,
            imgType: 'png'
          }
        };
        
        saveSignerEquipApply(data).then(res => {
          this.$toast({
            message: '签字确认成功！',
            duration: 1000
          });
          
          this.$router.push('/deviceApplyRecord');
        });
        
      }
    }
  }
</script>

<style lang="scss">
  .deviceApplyDetail {
    min-height: 100vh;
    background-color: #F8F8F8;
    
    .head {
      background-color: #FFF;
    }
    
    .list-block {
      font-size: .16rem;
      margin-top: .1rem;
      background-color: #fff;
    }
    
    .service-apply-detail {
      font-size: .16rem;
      margin-top: .1rem;
      
      .top {
        background-color: #fff;
        height: .45rem;
        display: flex;
        justify-content: space-between;
        align-items: center;
        padding: 0 .15rem;
        border-bottom: 1px solid #EBEBEB;
      }
      
      .details-container {
        background-color: #fff;
        margin-bottom: .05rem;
        padding-left: .2rem;
        border-bottom: 1px solid #EBEBEB;
        
        .detail-item {
          padding-right: .15rem;
          font-size: .14rem;
          display: flex;
          height: .54rem;
          justify-content: space-between;
          line-height: .54rem;
          border-bottom: 1px solid #EBEBEB;
          
          .left {
            width: 50%;
            padding-left: 2.5%;
            
            .tip-text {
              color: #888;
            }
          }
          
          .right {
            width: 40%;
            padding-left: 2.5%;
          }
          
          &:last-child {
            border-bottom: 0;
          }
        }
      }
      
    }
    
    .select-type {
      background-color: #fff;
      margin-top: .1rem;
      font-size: .16rem;
      
      .title {
        height: .45rem;
        padding: .1rem .15rem;
        border-bottom: 1px solid #dddddd;
      }
      
      .radio-select-box {
        padding-left: .15rem;
        border-bottom: 1px solid #dddddd;
        
        .radio-item {
          display: flex;
          justify-content: space-between;
          height: .52rem;
          line-height: .52rem;
          padding-left: .05rem;
          padding-right: .15rem;
          
          &:first-child {
            border-bottom: 1px solid #dddddd;
          }
          
          img {
            align-self: center;
          }
        }
      }
    }
    
  }
</style>



